#socialmediapost .socialmediapostswrapper {
    @include grid-row();
    padding-top: 30px;
    padding-bottom: 20px;
    float: none;
    @media screen and (max-width: 435px) {
        padding-bottom: 80px;
    }
    
    button.more {display: none !important;} //hide more button, remove this when it works again

    .socialmediafiltercontainer {
        width: auto;
        max-width: 70rem;
        margin: 0 $column-gutter/2;
        margin-bottom: $column-gutter/2;
        text-align: right;
        label {
        }
        select {
            border: 1px solid $border;
            background-color: lighten($border,15);
            padding: 10px 28px 10px 13px;
            background-position: 100% 50%;
        }
    }
    .socialmediaposts-content {
        @include grid-column(12);
        .grid-sizer {
            width: 100%;
        }
        .block {
            position: relative;
            display: inline-block;
            height: 165px;
            font-size: 14px;
            overflow: hidden;
            width: 93%;
            .poster {
                display: inline-block;
                a {
                    color: $white;
                    font-weight: bold;
                }
            }
            .date {
                display: inline-block;
                color: $white;
                &:before {
                    content: "";
                    display: inline-block;
                    border-left: 1px solid white;
                    margin: 0 7px;
                    height: 11px;
                }
            }
            span.message {
                display: block;
                padding: 15px 17px;
                a.link {
                    font-weight: normal;
                    color: $white;
                }
            }
            img.sitetype {
                position: absolute;
                bottom: 8px;
                right: 14px;
                z-index: 1;
            }
            .socialpostfooter {
                position: absolute;
                bottom: 0;
                left: 0;
                width: 100%;
                z-index: 0;
            }
            &.video, &.photo {
                .post_owner {
                    display: none;
                }
                .socialpostfooter {
                    display: none; 
                }
                img {
                    object-fit: cover;
                    min-width: 100%;
                    min-height: 100%;
                    &.sitetype {
                        min-width: auto;
                        min-height: auto;
                    }
                }
            }
            &.twitter {
                color: #FFFFFF;
                background:#0299C5;
                .socialpostfooter {
                    background: #1a91b7;
                    padding: 10px 17px;
                }
            }
            &.facebook {
                background: #004681;
                .socialpostfooter {
                    background: #013e70;
                    padding: 10px 17px;
                }
            }
        }
    }
    .linksocial-wrapper {
        display: none;
        .linksocial {
            padding-top: 25px;
            h2 {
                color: $white;
            }
        }
    }
    .load-more-button-wrapper {
        text-align:center;
        clear: both;

        .load-more-button {
            width: auto;
            margin: $column-gutter auto 0 auto;
        }
    }
    .fb-wrapper {
        width: auto;
        margin-left: $column-gutter/2;
        margin-right: $column-gutter/2;
    }
    .twitter-follow-button {
        margin: $column-gutter/2 0 $column-gutter $column-gutter/2;
    }
}

@media #{$medium-up} {
    #socialmediapost .socialmediapostswrapper {
        .fb-wrapper {
            width: 46%;
        }
        .socialmediafiltercontainer {
            float: right;
            label {
            }
            select {
                width: auto;
                padding-right: 28px;
            }
        }
        .socialmediaposts-content {
            .block {
                width: 46%;
                &.twitter {
                }
                &.facebook {
                    &.link {
                        
                    }
                }
                &.video, &.photo {
                    width: 21.5%;
                }
            }
        }
    } 
}

@media #{$large-up} {
    #socialmediapost .socialmediapostswrapper {
        padding-left: 0;
        padding-right: 0;
        .fb-wrapper {
            width: 31%;
            float: left;
            .fb-page {
                margin-bottom: $column-gutter;
            }
        }

        .socialmediafiltercontainer {
            padding-left: $column-gutter/2;
        }
        .socialmediaposts-content {
            .block {
                width: 31%;
                &.twitter {
                }
                &.facebook {
                }
                &.video, &.photo {
                    width: 14.5%;
                }
            }
        }
    }
    .twitter-follow-button {
        margin: 0 $column-gutter;
    }
}

