#wrapper > footer {
    width:100%;
    min-height: 100px;
    background: $footer;
    color: $footerText;
    @media print {
        display: none; 
    }
    section.footer {
        @include grid-row();
        padding: $column-gutter/2 0;
        a {
            color: $footerText;
            &:hover {
                text-decoration: underline;
            }
        } 
        nav {
            ul {
                list-style: none;
                margin-left: 0;
                font-size: 14px;
                line-height: 27px;
                @include clearfix();
                li {
                    a {
                        font-weight: 700;
                    }
                    .submenu {
                        margin-top: 10px;
                    }
                    ul {
                        li {
                            a {
                                font-weight: 500;
                            }
                        }
                    }
                }
            }
            & > ul > li {
                @include grid-column(12);
                padding: $column-gutter/2;
                border-bottom: 1px solid lighten($footer,10);
                float: none;
            }
        }
        .contentblocks, .footerblock {
            @include grid-column(12);
            p {
                font-size: 14px;
                line-height: 27px;
                &:first-child {
                    margin-bottom: 10px;
                }
            }
            .contentblock {
                h2, .label {
                    font-size: 14px;
                    line-height: 27px;
                    font-weight: 700;
                    color: $footerText;
                    display: block;
                }
                font-size: 14px;
                line-height: 27px;
                padding: $column-gutter/2;
                border-bottom: 1px solid lighten($footer,10);
            }
        }
        .socialmedia {
            ul {
                list-style: none;
                margin-left: 0;
                font-size: 14px;
                line-height: 27px;
                li {
                    display: inline-block;
                    margin-right: 20px;
                    &:first-child {
                        margin-bottom: 10px;
                        font-weight: 700;
                        display: block;
                    }
                    a {
                        font-weight: 700;
                        font-size: 0rem;
                        &:before {
                            font-size: 2rem;
                        }
                        &.twitter {
                            @include icon(twitter);
                        }
                        &.facebook {
                            @include icon(facebook);
                        }
                        &.linkedin {
                            @include icon(linkedin);
                        }
                    }
                }
            }
        }
    }
}

