@media #{$large-up} {
    #wrapper > footer {
        section.footer {
            & > nav {
                display: table;
                & > ul {
                    display: table-row;
                }
            }
            nav {
                & > ul > li {
                    @include grid-column(3);
                    border-right: 1px solid lighten($footer,10);
                    float: none; 
                    display: table-cell;
                    &:last-child {
                        border-right: none;
                    }
                }
            }
            .contentblocks, .footerblock {
                @include grid-column(3);
                float: none; 
                display: table-cell;
                vertical-align: top;
                .contentblock {
                    @include grid-column(3);
                    float: none; 
                    display: table-cell;
                    vertical-align: top;
                    border-right: 1px solid lighten($footer,10);
                    border-bottom: none;
                    &:nth-last-child(2) {
                        border-right: none;
                    }
                    &:last-child {
                        border-right: none;
                        width: 100%;
                        border-top: 1px solid lighten($footer,10);
                        display: block;
                    }
                    &:nth-child(2) {
                        border-top: none;
                    }
                }
            }
            .footerbottom {
                display: table;
                width: 100%;
                .footerbottomcontainer {
                    display: table-row;
                }
            }
        }
    }
}