@mixin transition($transition-property: all, $transition-time: 200ms, $method: ease-in-out) {
    transition: $transition-property $transition-time $method;
}

// Transforms

@mixin transform($args) {
    transform: $args;
}
@mixin transform-origin($args) {
    transform-origin: $args;
}
@mixin transform-style($style) {
    transform-style: $style;
}
@mixin rotate($deg:45deg){
    @include transform(rotate($deg));
    filter: "progid:DXImageTransform.Microsoft.Matrix(SizingMethod='auto expand', M11=0.7660444378852844, M12=-0.6427876353263855, M21=0.6427876353263855, M22=0.7660444378852844)"; /* IE8 */
}
@mixin scale($factor:.5){
    @include transform(scale($factor));
}
@mixin translate($x,$y){
    @include transform(translate($x,$y));
}
@mixin translate3d($x,$y,$z) {
    @include transform(translate3d($x,$y,$z));
}
@mixin translateHardware($x,$y) {
    @include translate($x,$y);
    transform: translate3d($x,$y,0);
}


@mixin btn($background: $main, $color: $white, $greyBtn:0){
    font-size: 1rem;
    font-weight: 900;
    padding: 10px 20px;
    background: $background;
    color: $color;
    display: inline-block;
    position: relative;
    cursor: pointer;
    overflow: hidden;
    border: none;
    border-radius: $btnRadius; 
    &:hover {
        background: lighten($background,10);
        color: $color;
    }
    &:focus {
        outline: 0;
    }
    @if $greyBtn ==  1 {
        &:hover {
            background: darken($thirdButton,10);
        }
    }

}

@mixin lightbtn($color: $main, $fontsize: 14px,  $display: inline-block, $icon:1, $active:0) {
    display: $display;
    position: relative;
    cursor: pointer;
    overflow: hidden;
    transition: all .3s ease-out;
    color: $color;
    background: transparent;
    border: 2px solid $color;
    font-size: rem-calc($fontsize);
    border-radius: 3px;
    line-height: 32px;
    font-family: $body-font-family;
    font-weight: normal;
    @if $icon ==  1 {
        padding: 0px 40px 0px 55px;
        &:before {
            content: "";
            display: inline-block;
            position: absolute;
            margin-left: -22px;
        }
        &:hover {
        }
    } @else {
        padding: 0px 40px;
    }
    &:hover, &.active {
        background: $color;
        color: $white;
        transition: all .3s ease-out;
    }
    &.active:hover {
        background: lighten($color,10);
    }
    @if $active == 1 {
        background: $color;
        color: $white;
        transition: all .3s ease-out;
        &:hover {
            background: lighten($color,10);
            border: 2px solid lighten($color,10);
        }
    }
}

@mixin print-utilities($media: print) {
  @if $media == print {
    .noprint, .no-print {
      display: none !important;
    }
    #{elements-of-type(block)} {
      &.print-only {
        display: block;
      }
    }
    #{elements-of-type(inline)} {
      &.print-only {
        display: inline;
      }
    }
  }
  @else {
    .print-only {
      display: none;
    }
  }
}
