.hamburger_icon {
    display: inline-block;
    max-width: 70px;
    border: none;
    background: inherit;
    cursor: pointer;
    padding: 28px 23px;
    height: 77px;
    margin-bottom: 0;
    outline: 0;
    vertical-align: top;
    .hamburger_layer {
        display: block;
        width: 24px;
        height: 3px;
        background: $hamburger;
        position: relative;
        animation-duration: 300ms;
        animation-timing-function: ease-in-out;
        &:before, &:after {
            display: block;
            width: inherit;
            height: 3px;
            position: absolute;
            background: inherit;
            left: 0;
            content: '';
            animation-duration: 300ms;
            animation-timing-function: ease-in-out;
        }
        &:before {
            bottom: 200%;
        }
        &:after {
            top: 200%;
        }
        &.hamburger_icon--from-arrow {
            animation-name: hamburger_icon--slide-from;
            &:before {
                animation-name: hamburger_icon--slide-before-from;
            }
            &:after {
                animation-name: hamburger_icon--slide-after-from;
            }
        }
        &.hamburger_icon--to-arrow {
            animation-name: hamburger_icon--slide;
            -webkit-animation-name: hamburger_icon--slide;
            -webkit-animation-fill-mode: forwards;
            &:before {
                animation-name: hamburger_icon--slide-before;
                -webkit-animation-name: hamburger_icon--slide-before;
                -webkit-animation-fill-mode: forwards;
            }
            &:after {
                animation-name: hamburger_icon--slide-after;
                -webkit-animation-name: hamburger_icon--slide-after;
                -webkit-animation-fill-mode: forwards;
            }
        }
    }
}