&.login #wrapper{
    div#main {
        padding-bottom: 50px;
        section {
            section.brandbox {
                display:none;
            }
            .articles_resetpassword-email-sent {
                background: transparent;
                border-bottom: none;
                .article_wrapper {
                    padding: 10px;
                }
            }
            div.panel.registered,
            div.panel.login {
                @include grid-row();
                padding: 0px;
                position: relative;
                background-color: transparent;
                margin-top: 5%;
                margin-left: auto;
                margin-right: auto;
                margin-bottom: 0;
                max-width: 750px;
                .alert-box {
                    margin-bottom: 30px;
                    &.notice .close {
                        color: #edb508;
                        top: 20px;
                    }
                }
                .left_panel,
                .right_panel {
                    width: 100%;
                    display: inline-block;
                    vertical-align: top;

                    .notice {
                        float: none;
                        margin-top: 0px;
                        margin-left: 0px;
                        width: 100%;
                        min-height: 57px;
                        padding-left: $column-gutter/2;
                    }

                    .form_user_registration p.notice,
                    .form_user_registration .missing_incorrect_notice .incorrect {
                        display: none;
                    }
                }
                .left_panel {
                    .form_user_login {
                        min-height: 422px;
                    }
                }
                .right_panel {
                    background-color: $white;
                }
                .intro_text {
                    padding: 15px;
                    h1,
                    h2 {
                        font-size: 1.2rem;
                        color: $main;
                    }
                    p {
                        color: $text;
                    }
                }

                .forgotpassword .intro_text {
                    padding: 18px 36px;
                }

                .panelswitcherContainer {
                    width: 100%;
                    float: left;
                    position: relative;
                    padding: 15px;
                    padding-right: 30px;
                    h2 {
                        color: $main;
                        font-size: 1.2rem;
                    }
                    p {
                        color: $text;
                        margin-bottom: 20px;
                    }
                    dl.panelswitcher {
                        dd {
                            &.nonactive {
                                display: none;
                                a.login {
                                    span {
                                        display: block;
                                    }
                                }
                            }
                            a.btn {
                                @include lightbtn($color: $main, $active:1);
                                display: block;
                                text-align: center;
                                margin-bottom: 21px;
                                span {
                                    font-weight: 700;
                                }
                                &.login {
                                    span {
                                        display: block;
                                    }
                                }
                            }
                        }
                    }
                }
                div.form_user,
                div.tabs-content p.information  {
                    padding: 18px 36px;
                    padding-top: 30px;
                }
            }
            div.login_registration {
                position: relative;
                perspective: 1600px;
                margin-left: auto;
                margin-right: auto;
                div.fb-login-button {
                    z-index: 901;
                }

                div.form_user {
                    padding: 18px 36px;
                    background: $white;
                    width: 100%;

                    h3 {
                        font-size: rem-calc(16px);
                    }

                    form {
                        padding: 0;
                        label {
                            margin-bottom: 8px;
                            margin-left: 0px;
                            span {
                                font-size: .9rem;
                            }
                        }
                        .formfield.submit_field div.formactions {
                            a.resetpassword {
                                display: block;
                                margin-bottom: 18px;

                                color: $link;
                                background: transparent;
                                border: 0;
                                font-size: 0.875rem;
                                border-radius: 0;
                                line-height: inherit;
                                padding: 0px;
                            }
                            button {
                                display: block;
                                width: 100%;
                                margin-left: 0;
                            }
                        }
                        .required_fields_text {
                            display: none;
                        }
                        .accept_conditionsfield {
                            margin-bottom: 8px;
                            label {
                                color: $text;
                            }
                        }
                        input {
                            -webkit-box-shadow: inset 0 0px 0px rgba(0, 0, 0, 0), inset 0 0 0 50px white;
                        }
                        .formfield {
                            padding-bottom: 10px;
                            padding-right: 0;
                            &.submit_field {
                                margin-top: 0;
                            }
                            &.accept_conditionsfield {
                                label {
                                    span {
                                        font-size: 1rem;
                                    }
                                }
                            }
                        }
                    }
                    &#login {
                        background: $white;
                        position: absolute;
                        top: 0;
                        left: 0;
                        z-index: 900;

                        @include transform(rotateX(0deg) rotateY(0deg));
                        @include transform-style(preserve-3d);

                        -webkit-backface-visibility: hidden;
                        -moz-backface-visibility: hidden;
                        backface-visibility: hidden;

                        @include transition();
                    }
                    &#registration {
                        background: $white;
                        float: none;
                        position: absolute;
                        top: 0;
                        left: 0;
                        z-index: 800;

                        -webkit-transform: rotateY(-180deg);
                        -moz-transform: rotateY(-179deg); /* setting to 180 causes an unnatural-looking half-flip */
                        transform: rotateY(-179deg);

                        @include transform-style(preserve-3d);
                        backface-visibility: hidden;

                        @include transition();
                    }
                }
                &.flip div.form_user#login {
                    z-index: 900;

                    transform: rotateY(180deg);
                }
                &.flip div.form_user#registration {
                    z-index: 1000;
                    @include transform(rotateX(0deg) rotateY(0deg));
                }
            }
            div.panel.forgotpassword {
                background: $white;
                border-radius: 5px;
                div.form_user {
                    padding: 18px 36px;

                }
            }
            div.forgotpassword-sent {
                .article_wrapper {
                    padding: 18px 36px;
                    h1 {
                        font-size: 1.2rem;
                    }
                }
            }
            div.copyright {
                position: absolute;
                bottom: 120px;
                right: 20px;
                color: $white;
            }
            .article-contentblocks.article-contentblocks .contentblocks {
                padding: 0;
                .contentblock {
                    margin: 0;
                    .richtext {
                        line-height: 27px;
                        p, li {
                            line-height: 27px;
                        }
                    }
                }
            }
        }
    }
}

#dynamicform {
    position: fixed;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.3);
    z-index: 998;
    top: 0;
    left: 0;
    .dynamicform-wrapper {
        z-index: 999;
        position: fixed;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        background: $white;
        width: 100%;
        overflow-y: scroll;
        height: 100%;
        overflow: auto;
         p {
            font-size: rem-calc(14);
        }
        .close {
            position: absolute;
            top: 0;
            right: 0;
            background-color: $main;
            padding: 5px 10px;
            color: $white;
            @include icon(cancel);
        }
        .notice {
            background-color: $notificationBar;
            color: $notificationText;
            border-radius: 5px;
            padding: $column-gutter/2;
            margin: $column-gutter $column-gutter*2 $column-gutter/2 $column-gutter;
        }
        .formcontainer {
            background: $white;
            .formgroup {
                padding: $column-gutter;
            }
            .resetpassword {
                float: right;
            }
            form {
                [type="checkbox"]:checked + label:before {
                    -webkit-backface-visibility: visible;
                }
                .formfield {
                    .formactions {
                        button {
                            width: 100%;
                            @include btn;
                            margin: 10px 0;
                        }
                    }
                }
                input[type="email"],input[type="password"],input[type="text"] {
                    padding-right: 30px;
                }
                [type="checkbox"] {
                    left: inherit;
                    opacity: 0;
                }
                .text_field, .password_field {
                    &:before {
                        position: absolute;
                        top: 1rem;
                        right: 5px;
                        color: $label;
                    }
                }
                .usernamefield, .emailfield {
                    @include icon(at);
                }
                .firstnamefield, .middlenamefield, .lastnamefield {
                    @include icon(face);
                }
                .answerfield {
                    @include icon(message);
                }
                .password_field {
                    @include icon(lock);
                }
            }
            a {
                color: $main;
            }
            .extralink {
                margin-top: $column-gutter/2;
                padding-top: $column-gutter/2;
                border-top: 1px solid $border;
                p {
                    margin-bottom: 0;
                }
            }
            #recaptcha {
                transform:scale(0.84);
                transform-origin:0 0;
                margin-top: 18px;
            }
        }
    }
}

@media #{$medium-up} {
    &.login #wrapper{
        div#main{
            padding-bottom: 50px;
            section {
                .brandbox {
                    display: block;
                    z-index: -1;
                    #slickslider {
                        position: fixed;
                        height: 100%;
                    }
                }
                section.brandbox {
                    display: block;
                }
                div.panel.registered,
                div.panel.login {
                    .left_panel,
                    .right_panel {
                        width: 50%;
                    }
                    .left_panel {
                        border-right: 1px solid $border;
                    }
                    .panelswitcherContainer {
                        float: right;
                        border-top: none;
                        dl.panelswitcher {
                            right: -20px;
                        }
                    }
                }
                div.panel.forgotpassword {
                }
                div.copyright {
                    bottom: 90px;
                }
            }
        }
    }

    #dynamicform {
        .dynamicform-wrapper {
            .registration_form .formgroup {
                @include grid-column(6);
                padding: $column-gutter;
                &:nth-child(2) {
                    border-left: 1px solid $border;
                }
            }
        }
    }
}

@media #{$large-up} {
    #dynamicform {
        .dynamicform-wrapper {
            height: auto;
            width: auto;
        }
    }

    &.login #wrapper{
        display: flex;
        flex-flow: row wrap;
        > #header {
            flex: 1 0 100%;
        }
        > #main {
            flex: 1 0 100%;
        }
        > footer{
            flex: 1 0 100%;
        }
    }
}