@keyframes ripple {
    100% {
        opacity: 0; 
        transform: scale(2.5);
    }
}

@keyframes hamburger_icon--slide {
    0% {
    }
    100% {
        transform: rotate(180deg);
    }
}

@keyframes hamburger_icon--slide-after {
    0% {
    }
    100% {
        transform: rotate(-45deg);
        margin: 0% 37%;
        width: 75%;
    }
}

@keyframes hamburger_icon--slide-before {
    0% {
    }
    100% {
        transform: rotate(45deg);
        margin: 0% 37%;
        width: 75%;
    }
}

@keyframes hamburger_icon--slide-from {
    0% {
        transform: rotate(-180deg);
    }
    100% {
    }
}

@keyframes hamburger_icon--slide-after-from {
    0% {
        transform: rotate(-45deg);
        margin: 0% 37%;
        width: 75%;
    }
    100% {
    }
}

@keyframes hamburger_icon--slide-before-from {
    0% {
        transform: rotate(45deg);
        margin: 0% 37%;
        width: 75%;
    }
    100% {
    }
}

@keyframes animFadeIn {
    0% { opacity: 0; }
    100% { opacity: 1; }
}
@keyframes animMoveFromLeft {
    0%  { 
        opacity: 0;
        left: -110%; 
    }
    100% { 
        opacity: 1;
        left: 0%; 
    }
} 

@keyframes animMoveToLeft {
   0%  { 
        opacity: 1;
        left: 0%; 
    }
    100% { 
        opacity: 0;
        left: -110%; 
    }
} 

.animated {
    animation-timing-function: ease-out;
    animation-duration: 1s;
    animation-fill-mode: both;
}

@mixin animated{
    animation-timing-function: ease-out;
    animation-duration: 1s;
    animation-fill-mode: both;
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

.fadeIn {
  animation-name: fadeIn;
}

@keyframes fadeInDown {
  from {
    opacity: 0;
    transform: translate3d(0, -100%, 0);
  }

  100% {
    opacity: 1;
    -webkit-transform: none;
    transform: none;
  }
}

.fadeInDown {
  animation-name: fadeInDown;
}

@keyframes fadeInLeft {
  from {
    opacity: 0;
    transform: translate3d(-100%, 0, 0);
  }

  100% {
    opacity: 1;
    transform: none;
  }
}

.fadeInLeft {
  animation-name: fadeInLeft;
}

@keyframes fadeInRight {
  from {
    opacity: 0;
    transform: translate3d(100%, 0, 0);
  }

  100% {
    opacity: 1;
    transform: none;
  }
}

.fadeInRight {
  animation-name: fadeInRight;
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translate3d(0, 100%, 0);
  }

  100% {
    opacity: 1;
    transform: none;
  }
}

.fadeInUp {
  animation-name: fadeInUp;
}

@keyframes fadeOutDown {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
    transform: translate3d(0, 100%, 0);
  }
}

.fadeOutDown {
  animation-name: fadeOutDown;
}

@keyframes fadeOutUp {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
    transform: translate3d(0, -100%, 0);
  }
}

.fadeOutUp {
  animation-name: fadeOutUp;
}