.brandbox{
    background: $main url("/images/abstract.png") no-repeat center center;
    background-size: cover;
    color: $white;
    &.related {
        background-color: darken($border,20);
        .brandboxContent {
            h2 {
                color: $white;
                text-align: center;
                font-size: 30px;
                padding: 40px 0;
            }
        }
    }
    .brandboxContent {
        @include grid-row();
        h1 {
            color: $white;
            text-align: center;
            font-size: 45px;
            padding: 98px 0;
        }
        &.has-banners {
            max-width: inherit;
            padding: 0;
        }
        &.activity-detail {
            max-width: inherit;
            padding: 115px 0;
            background-size: cover;
            background-position: center center;
        }
    }
    .home #slickslider {
        height: 410px;
        .brandboxitem{
            &.slick-active .brandboxitemContent .brandboxitemContentText{
                h2 {
                    animation: fadeInRight 1.0s;
                }
                p {
                    animation: fadeInRight 1.5s;
                }
                a.btn {
                    animation: fadeInRight 1.7s;
                }
            }
        }
    }
    #slickslider {
        width: 100%;
        overflow: hidden;
        color: $white;
        height: 260px;
        .slick-arrow {
            position: absolute;
            z-index: 1;
            top: 50%;
            transform: translateY(-50%);
            font-size: 0rem;
            background-color: white;
            padding: 15px 2px;
            color: $text;
            visibility: hidden;
            @include transition();
            &:hover {
                @include transition();
            }
            &.slick-prev {
                left: 0;
                border-radius: 0 3px 3px 0;
                @include icon(arrow-left){
                    font-weight: bold;
                };
                &:hover {
                    padding-left: 10px;
                }
            }
            &.slick-next {
                right: 0;
                border-radius: 3px 0 0 3px;
                @include icon(arrow-right){
                    font-weight: bold;
                };
                &:hover {
                    padding-right: 10px;
                }
            }
            &:before {
                font-size: 1.6rem;
                vertical-align: middle;
            }
        }
        .brandboxitem{
            height: 100%;
            background-size: cover;
            background-position: center center;
            background-repeat: no-repeat;
            overflow: hidden;
            video, iframe {
                display: none;
            }
            &.slick-active .brandboxitemContent .brandboxitemContentText{
                h2 {
                    animation: fadeIn 1.0s;
                }
                p {
                    animation: fadeIn 1.5s;
                }
                a.btn {
                    animation: fadeIn 1.7s;
                }
            }
            .brandboxitemContent{
                padding: $column-gutter/2;
                position: absolute;
                bottom: 0;
                width: 100%;
                background: rgba(0,0,0,0.18);
                .brandboxitemContentText{
                    h2{
                        color: $white;
                        font-weight: 900;
                        text-shadow: 1px 1px 1px rgba(0,0,0,0.4);
                        text-transform: uppercase;
                        font-size: 24px;
                        margin-bottom: 0px;
                        line-height: 27px;
                        position: relative;
                        left: 0;
                    }
                    p{
                        font-size: 16px;
                        max-height: 51px;
                        position: relative;
                        overflow: hidden;
                        left: 0;
                        &:after {
                            content: '\2026';
                            display: inline-block;
                            position: absolute;
                            bottom: 0;
                            right: 0;
                        }
                    }
                    .btn {
                        left: 0;
                        position: relative;
                        background: lighten($main,10);
                        margin-bottom: 20px;
                        float:right;
                        margin-bottom: $column-gutter/2;
                        text-transform: uppercase;
                        padding: 10px 20px;
                        border-radius: 2px;
                        box-shadow: 1px 1px 1px rgba(0,0,0,0.2);
                    }
                }
            }
            &.compositeform {
                .overlay {
                    background-color: #000;
                }
                form {
                    padding: 0;
                    height: 100%;
                    .querystringfield {
                        padding: 0;
                        width: 66%;
                        display: inline-block;
                        vertical-align: top;
                        input[type="text"] {
                            background: $white;
                            color: $text;
                            padding-left: $column-gutter/2;
                        }
                        .bar {
                            display: none;
                        }
                        label {
                            left: $column-gutter/2;
                            top: 10px;
                            -webkit-backface-visibility: hidden;
                            &.active {
                                opacity: 0;
                            }
                        }
                    }
                    .formfield.submit_field {
                        margin: 0;
                        padding: 0;
                        width: 29%;
                        display: inline-block;
                        button[type=submit] {
                            margin: 0;
                            border: 0;
                            border-radius: 0;
                            display: block;
                            width: 100%;
                            height: 3rem;
                            font-weight: 700;
                            font-size: 16px;
                            text-transform: lowercase;
                            padding: 8px 0;
                            float: none;
                            &:before {
                                content: "";
                            }
                        }
                    }

                }
                .distancefield, .cityfield, .formcontainer:last-child {
                    display: none;
                }
            }
        }
    }
}

 .slick-slider {
    position: relative;
    display: block;
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    -ms-touch-action: pan-y;
    touch-action: pan-y;
    -webkit-tap-highlight-color: transparent;
}
.slick-list {
    height: 100%;
    position: relative;
    overflow: hidden;
    display: block;
    margin: 0;
    padding: 0;

    &:focus {
        outline: none;
    }

    &.dragging {
        cursor: pointer;
        cursor: hand;
    }
}
.slick-track {
    position: relative;
    left: 0;
    top: 0;
    display: block;
    height: 100%;
}
.slick-slide {
    float: left;
    height: 100%;
    min-height: 1px;
}
.slick-dots {
    position: absolute;
    bottom: 10px;
    list-style: none;
    display: block;
    padding: 0;
    padding-left: $column-gutter/2;
    margin: 0;
    width: 100%;
    li {
        position: relative;
        display: inline-block;
        height: 20px;
        width: 20px;
        margin: 0 5px;
        padding: 0;
        cursor: pointer;
        button {
            border: 5px solid $white;
            background: $border;
            display: block;
            height: 10px;
            width: 10px;
            border-radius:10px;
            outline: none;
            line-height: 0px;
            font-size: 0px;
            color: transparent;
            padding: 4px;
            cursor: pointer;
        }
        &.slick-active button{
            background: $main;
            opacity: 1;
        }
    }
}

.actionbox {
    background: $actionBox;
    .actionbox_container {
        @include grid-row();
        .contentblock {
            display: block;
            @include grid-column(12);
            color: $white;
            h2, h3 {
                color: $white;
            }
            a {
                color: $white;
            }
            .contentblockcontent {
                display: block;
                margin: $column-gutter 0;
                text-align: center;
                &.emptycontentblockcontent {
                    display: none;
                }
            }
            h2 {
                font-style: italic;
                font-weight: 500;
                font-size: 1.3rem;
            }
            .btn {
                @include lightbtn($color: $white, $icon:0 );
                margin-left: 10px;
                font-weight: 700;
                text-transform: uppercase;
                &:hover {
                    color: $actionBox;
                }
            }
            .richtext {
                p {
                    line-height: 32px;
                    margin: 0;
                }
            }
        }
    }
}

@media #{$medium-up} {
    .brandbox{
        .home #slickslider{
            height: 350px;
        }
        #slickslider{
            .brandboxitem{
                position: relative;
                background-position: center center;
                background-size: cover;
                video, iframe {
                    display: block;
                }
                .overlay {
                    opacity: 0.5;
                    position: absolute;
                    height: 100%;
                    width: 50%;
                    top: 0;
                    z-index: 0;
                    left: 0;
                }
                .brandboxitemContent{
                    @include grid-row();
                    position: static;
                    bottom: 0;
                    background: none;
                    height: 100%;
                    .brandboxitemContentText{
                        @include grid-column(6);
                        padding-left: 0;
                        position: relative;
                        text-align: left;
                        z-index: 2;
                        top: 50%;
                        left: 0;
                        transform: translateY(-50%);
                        p {
                            max-height: inherit;
                            overflow: auto;
                            &:after {
                                display: none;
                            }
                        }
                        .btn {
                            background: transparent;
                            border: 2px solid $white;
                            border-radius: 5px;
                            margin-bottom: inherit;
                            float: left;
                            padding: 10px 30px;
                            &:hover {
                                background: $main;
                                border-color: $main;
                                color: $white;
                            }
                        }
                    }
                }
                &.photo {
                    .overlay {
                        display: none;
                    }
                }
                &.fullwidth .brandboxitemContent{
                    .overlay {
                        width: 100%;
                        left: 0;
                    }
                    .brandboxitemContentText .btn {
                        float: none;
                    }
                }
                &.bannervideotextlabel, &.banneryoutubetextlabel, &.fullwidth {
                    .brandboxitemContent {
                        .brandboxitemContentText {
                            width: 100%;
                            text-align: center;
                        }
                    }
                }
                .brandboxitemImage{
                    @include grid-column(6);
                    display: block;
                    float: right;
                    height: 100%;
                    background-size: cover;
                }
                video, iframe {
                    min-width: 100%;
                    min-height: 100%;
                    transform: translate(-50%,-50%);
                    position: absolute;
                    left: 50%;
                    top: 50%;
                }
                &.compositeform {
                    form {
                        .formfield.submit_field {
                            button[type=submit] {
                                padding: 0;
                                float: none;
                                background-color: $main;
                                border: none;
                                border-radius: 0;
                                text-transform: lowercase;
                                padding: 8px 0 8px 19px;
                                @include icon(search){
                                    font-size: 16px;
                                    position: absolute;
                                    left: 11px;
                                };
                            }
                        }
                    }
                }
            }
        }
    }
    .slick-dots{
        @include grid-row();
        position: relative;
        margin-top: -40px;
        margin-bottom: 10px;

    }
}

@media #{$large-up} {
    .brandbox{
        .brandboxContent {
            &.activity-detail {
                padding: 225px 0;
            }
        }
        .home #slickslider{
            height: 400px;
        }
        #slickslider{
            .slick-arrow {
                visibility: visible;
            }
            .brandboxitem{
                .brandboxitemContent{
                    .brandboxitemContentText{
                        h2{
                            font-size: 34px;
                            margin-bottom: 0px;
                            line-height: 40px;
                        }
                        p{
                            font-size: 20px;
                        }
                    }
                    .readMore{

                    }
                }
                .brandboxitem .brandboxitemContent .brandboxitemContentText .readMore{
                    float:left;
                }
            }
        }
    }
}

@media #{$xlarge-up} {
    .brandbox{
        .home #slickslider{
            height: 450px;
        }
    }
}
