::selection {
    background: $main;
    color: $white;
}

.paperbtn {
    position: relative;
    overflow: hidden;
    .ink {
        display: block; 
        position: absolute;
        background: rgba(255,255,255,0.2);
        border-radius: 100%;
        transform: scale(0);
    }

    .ink.animate {
        animation: ripple 0.65s linear;
    }
}

.card {
    border-style: solid;
    border-width: 1px;
    border-color: $cardBorder;
    margin-bottom: $column-gutter;
    padding: $column-gutter/2;
    background: $cardBg;
    box-shadow: 0.868px 4.924px 5.34px 0.66px rgba(0, 0, 0, 0.03);
}

a.card:hover {
    box-shadow: 0.868px 4.924px 5.34px 0.66px rgba(0, 0, 0, 0.11);
}

@mixin card {
    border-style: solid;
    border-width: 1px;
    border-color: $cardBorder;
    margin-bottom: $column-gutter;
    padding: $column-gutter/2;
    background: $cardBg;
    box-shadow: 0.868px 4.924px 5.34px 0.66px rgba(0, 0, 0, 0.03);
    &:hover {
        box-shadow: 0.868px 4.924px 5.34px 0.66px rgba(0, 0, 0, 0.11);
    }
}

.btn {
    @include btn();
}
