#wrapper {
    div#main {
        section {
            div.wrapper.incomplete {
                border-left: 5px solid $error;
            }
            div.overview {
                &.employer {
                    background: $white;
                }
                &.profile_overview {
                    &.my-applications {
                        display: none;
                        .actions {
                            display: none;
                        }
                        .profile_fields {
                            border-top: none;
                        }
                        table {
                            width: 100%;
                            th {
                                text-align: left;
                            }
                            tr {
                                border-bottom: 1px solid $border;
                                td {
                                    padding-top: 10px;
                                    padding-bottom: 10px;
                                }
                                &.header,
                                &.lastcb {
                                    border-bottom: none;
                                }
                            }
                        }
                    }
                }
                &.favorites {
                    .overview-inner {
                        h2 {
                            margin-bottom: 30px;
                        }
                        .row {
                            position: relative;
                            margin-bottom: 10px;
                            margin-top: 10px;
                            .additionalData {
                                padding-left: 30px;
                                span.label {
                                    margin-right: 10px;
                                }
                            }
                        }
                        #favorite_wrapper {
                            top: 3%;
                        }
                        a.vacancy_link {
                            vertical-align: top;
                            margin-left: 30px;
                        }
                    }
                }
                .fields {
                    &.profile_fields {
                        border-top: 1px solid #cbcbcb;
                        .fieldsgroup_linkedin {
                            #linkedin {
                                .wrapper {
                                    padding-left: 0;
                                    p {
                                        padding-left: 0;
                                    }
                                    a {
                                        padding-right: 20px;
                                        &:after {
                                            background-image: none;
                                        }
                                    }
                                }
                            }
                        }
                    }
                    .fieldsgroup {
                        border-bottom: 1px solid $devider;
                        padding: $column-gutter 0;
                        &:nth-child(2n+1){
                            background: $white;
                        }
                        .divider {
                            border-left: 1px solid $border;
                            display: inline-block;
                            width: 2px;
                            margin-left: 15px;
                            margin-right: 15px;
                            height: 45px;
                        }
                        &.fieldsgroup_jobalert {
                            .overview {
                                &.empty {
                                    .title {
                                        display: none;
                                    }
                                }
                            }
                        }
                        .overview.empty {
                            @media print {
                                display: none !important;
                            }
                        }
                        .Vacancyapplication_overview.empty {
                            display: none;
                        }
                        .field:last-child {
                            border-bottom: none;
                        }
                        &.fieldsgroup_training {
                            color: $text;
                        }
                        &.fieldsgroup_candidate {
                            .value {
                                line-height: 30px;
                                padding-top: 0.8%;
                                padding-bottom: 1%;
                            }
                        }
                        &.fieldsgroup_vacancyapplication {
                            padding: 0;
                        }
                        &.fieldsgroup_vacancy {
                            .value {
                                &.applications {
                                    background-color: #EDEDED;
                                    border-top: 1px solid $border;
                                    span.disabled {
                                        color: $text;
                                        float: right;
                                    }
                                }
                                &.bottom {
                                    position: relative;
                                    .actions {
                                        position: absolute;
                                        right: 5px;
                                        bottom: -37px;
                                    }
                                }
                            }
                        }
                        &.fieldsgroup_skill {
                            @include grid-row();
                            border-bottom: none;
                            .fields {
                                .field {
                                    display: inline-block;
                                    background-color: #EDEDED;
                                    border-radius: 5px;
                                    border: 1px solid $border;
                                    margin-left: $column-gutter/2;
                                    padding-bottom: 0;
                                    .wrapper {
                                        padding: 0;
                                        text-align: center;
                                        line-height: 30px;
                                        .label {
                                            width: 100%;
                                        }
                                        .actions {
                                            display: none;
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
                & .wrapper > .title {
                    padding-top: 36px;
                }
                .title {
                    @include grid-row();
                    line-height: 70px;
                    h2 {
                        @include grid-column(6);
                        width: auto;
                        float: none;
                        line-height: 70px;
                    }
                }
                .actions {
                    float: right;
                    margin-top: 18px;
                    @media print {
                        display: none !important;
                    }
                    a {
                        @include lightbtn();
                        border: 2px solid $iconButton;
                        color: $iconButton;
                        @include icon('edit');
                        margin-right: $column-gutter/2;
                        &:last-child {
                            margin-right: 0;
                        }
                        &:hover {
                            background-color: $iconButton;
                        }
                        &.preview {
                            margin-right: 10px;
                        }
                        &.add {
                            @include icon('add');
                        }
                        &.delete {
                            @include icon('delete');
                        }
                        &.block {
                            @include icon('lock');
                        }
                        &.unblock {
                            @include icon('lock_open');
                        }
                    }
                }
                .field {
                    border-bottom: 1px solid $fieldDevider;
                    @include clearfix();
                    &:last-child {
                        border-bottom: none;
                    }
                    &.skill_foreignfield {
                        .subvalue {
                            @media print {
                                line-height: 15px !important;
                            }
                            &.comment_subvalue,
                            &.level_subvalue {
                                @media print {
                                    display: none !important;
                                }
                            }
                        }
                        .label {
                            @media print {
                                display: none !important;
                            }
                        }
                    }
                    &.profile_motivation_field {
                        padding: 20px 0;
                        .wrapper{
                            line-height: 22px;
                        }
                    }
                    &.vacancyapplication_foreignfield {
                        padding: $column-gutter/2 0;
                        .label {
                            font-weight: 700;
                        }
                        .value {
                            .subvalue {
                                float: right;
                            }
                            a .subvalue {
                                float: left;
                            }
                        }
                    }
                    .wrapper {
                        @include grid-row();
                        line-height: 48px;
                        padding-left: 0;
                        @media print {
                            line-height: 15px !important;
                            padding-left: 0 !important;
                        }
                        .label {
                            @include grid-column(12);
                            span {
                                display: inline-block;
                            }
                        }
                        .value {
                            @include grid-column(12);
                            .subvalue {
                                display: block;
                                @media print {
                                    margin-top: 5px !important;
                                    margin-bottom: 5px !important;
                                }
                                a {
                                    font-size: 1.2rem;
                                    font-weight: 700;
                                    color: $main;
                                    &.btn {
                                        color: $white;
                                    }
                                }
                                &.longvarchar_subvalue {
                                    line-height: 20px;
                                }
                            }
                        }

                        .actions {
                            @include grid-column(12);
                            text-align: right;
                            a.edit {
                                @include lightbtn($color: $main);
                                @include icon(edit);
                            }
                            a.delete {
                                @include lightbtn($color: $error);
                                @include icon(delete);
                                margin-left: 18px;
                            }
                        }
                    }
                }
                .fieldsgroup {
                    &.fieldsgroup_jobpreferences,
                    &.fieldsgroup_availability {
                        .field {
                            .wrapper {
                                .label {
                                    width: 35%;
                                }
                                .value {
                                    width: 65%;
                                }
                            }
                        }
                    }
                }
                .wrapper {
                    @include grid-row();
                    padding-left: 0;
                    .notice {
                        @include grid-column(12);
                        border:1px solid #eff1c0;
                        background-color:#fffeec;
                        padding: 15px;
                        margin-top: 10px;
                        min-height: 25px;
                        height: 100%;
                        color: $text;
                        margin-left: 0;
                        position: relative;
                        padding-left: 50px;
                        &:before {
                            position: absolute;
                            left: 20px;
                        }
                    }
                    &.addjobalert {
                        margin-top: 36px;
                    }
                }
                &.dashboard_overview {
                    margin-top: 35px;
                }
                &.account_overview {
                    @include grid-row();
                    .account_fields{
                        @include grid-column(12);
                        .field{
                            padding-bottom: 15px;
                            margin-bottom: 30px;
                        }
                    }
                    & + .actions{
                        @include grid-column(12);
                        a.btn {
                            @extend .button;
                            width: 45%;
                            text-align: center;
                            & + a.btn{
                                float: right;
                            }
                        }
                    }
                }
            }
            .module-container {
                background: $module-background;
                @include clearfix;
                & + .pager {
                    background: $white;
                    padding: $column-gutter/2 0;
                    border: 1px solid $border;
                    border-left: 0;
                    border-right: 0;
                }
                #filter {
                    padding-top: $column-gutter;
                }
                .items {
                    padding-top: $column-gutter;
                    .managementbar {
                        margin: $column-gutter 0;
                        @include clearfix;
                        h2 {
                            display: inline-block;
                        }
                        .btn {
                            float: right;
                        }
                    }
                    .item {
                        @include clearfix;
                        margin-bottom: $column-gutter;
                        & > a, .container {
                            display: block;
                            background-color: $white;
                            border: 1px solid $border;
                            @include transition();
                            @include clearfix;
                            &:hover {
                                box-shadow: 0.868px 4.924px 5.34px 0.66px rgba(0, 0, 0, 0.11);
                                .bottom-group .more {
                                    @include transition();
                                    background: $module-main;
                                    color: $white;
                                    border-left: 1px solid darken($module-main,10);
                                }
                            }
                        }
                        .container {
                            background-color: $white;
                        }
                        .fields-group {
                            background: $module-item-background;
                        }
                        .top-group {
                            background: $module-main;
                            color: $white;
                            font-weight: 700;
                            text-shadow: 1px 1px 1px rgba(0,0,0,0.3);
                            font-size: 1.2rem;
                            h1 {
                                color: $white;
                                font-weight: 700;
                                text-shadow: 1px 1px 1px rgba(0,0,0,0.3);
                                font-size: 1.2rem;
                                margin: 0;
                            }
                            span, label {
                                padding: $column-gutter/2;
                                display: block;
                                @for $i from 2 through 4 {
                                    &:nth-child(#{$i}) {
                                        background: lighten(darken($module-main,50),$i*12);
                                    }
                                }
                            }
                        }
                        .photo_wrapper {
                            background-size: cover;
                            background-position: center center;
                            height: 150px;
                        }
                        .main-group {
                            color: $text;
                            line-height: 1.6rem;
                            padding: $column-gutter/2;
                            p {
                                margin-bottom: 0;
                                font-size: 0.95rem;
                            }
                        }
                        .bottom-group {
                            background: $module-footer;
                            color: $text;
                            border-top: 1px solid $border;

                            span, a, label, .side {
                                padding: $column-gutter/2;
                                display: inline-block;
                                border-right: 1px solid $border;
                                border-bottom: 1px solid $border;
                                vertical-align: top;
                            }
                            .side {
                                position: relative;
                            }
                            span {
                                &:before {
                                    display: inline-block;
                                    vertical-align: top;
                                }
                            }
                            span, a {
                                &.more, &.btn {
                                    @include transition();
                                    background-color: $white;
                                    border-right: 0;
                                    border-left: 1px solid $border;
                                    text-transform: uppercase;
                                    color: $module-main;
                                    font-weight: 700;
                                }
                            }
                            label {
                                width: 50%;
                                display: inline-block;
                                border-right: 0;
                                & + span, & + a.inline {
                                    width: 50%;
                                    display: inline-block;
                                    vertical-align: top;
                                }
                            }
                            a {
                                margin: 0;
                                border-radius: 0;
                                border-top: 0;
                                &:hover {
                                    background-color: $module-main;
                                    color: $white;
                                }
                                &:before {
                                    display: inline-block;
                                    margin-right: $column-gutter/2;
                                }
                                 &.foreignview {
                                    @include icon(add);
                                }
                                &.view {
                                    @include icon(eye);
                                }
                                &.edit {
                                    @include icon(edit);
                                }
                                &.close {
                                    @include icon(delete);
                                }
                            }
                            .formcontainer {
                                background: none;
                                display: block;
                                h1 {
                                    display: none;
                                }
                                form {
                                    .formfield {
                                        label {
                                            padding: 0;
                                            border-right: 0;
                                            span {
                                                border-right: 0;
                                            }
                                            &:after {
                                                content: none;
                                            }
                                        }
                                        select {
                                            width: 50%;
                                            display: inline-block;
                                            padding: 0.9375rem;
                                        }
                                    }
                                }
                            }
                            .city,
                            .workinglocation {
                                @include icon(mapmarker){
                                    margin-right: 10px;
                                };
                            }
                            .amounthours {
                                @include icon("time"){
                                    margin-right: 10px;
                                };
                            }
                            .tariff {
                                @include icon(euro){
                                    margin-right: 10px;
                                    vertical-align: top;
                                };
                            }
                        }
                    }
                }
                &.layout-blocks {
                    section.items {
                        .item {
                            .container > a {
                                background-color: $black;
                            }
                            a {
                                padding: 0;
                                position: relative;
                                overflow: hidden;
                                -webkit-backface-visibility: hidden;
                                &:hover {
                                    @include transition;
                                    .photo_wrapper {
                                        @include transition;
                                        opacity: .6;
                                        transform: scale(1.1);
                                    }
                                    h1 {
                                        color: $module-main;
                                    }
                                    .bottom-group {
                                        & > span {
                                            &:last-child {
                                                @include transition;
                                                background: $module-main;
                                                color: $white;
                                                border-color: darken($module-main, 10);
                                            }
                                        }
                                    }
                                }
                                .top-group {
                                    position: absolute;
                                    bottom: 0;
                                    left: 0;
                                    width: 100%;
                                    z-index: 1;
                                    background: none;
                                    word-break: break-word;
                                    h1 {
                                        font-size: 2rem;
                                        text-shadow: 1px 1px 1px rgba(0,0,0,0.3);
                                        text-transform: none;
                                    }
                                }
                                .photo_wrapper {
                                    @include transition;
                                }
                                .bottom-group {
                                    position: relative;
                                    padding: 0;
                                    border-top: 0;
                                    font-size: 1rem;
                                    -webkit-backface-visibility: hidden;
                                    & > span {
                                        display: block;
                                        padding: $column-gutter/2;
                                        border: 1px solid darken($border,10);
                                        border-right: 0;
                                        border-top: 0;
                                        text-transform: none;
                                        white-space: nowrap;
                                        overflow: hidden;
                                        text-overflow: ellipsis;
                                        &.spec {
                                            font-weight: 700;
                                            span {
                                                font-weight: 500;
                                            }
                                        }
                                        &:last-child {
                                            @include transition;
                                            background: darken($module-footer,5);
                                            border-left: 1px solid darken($module-footer,10);
                                            border-right: 1px solid darken($border,10);
                                        }
                                        span {
                                            display: inline;
                                            padding: 0;
                                            padding-left: 5px;
                                            float: none;
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
                &.mode-uniquerequested {
                    .wrapper {
                        background: $module-background;
                    }
                    .items .item {
                        margin: 0;
                    }
                    .items .item {
                        .accented_details {
                            padding: $column-gutter;
                            margin: $column-gutter (-$column-gutter);
                            margin-bottom: $column-gutter;
                            background: $main;
                            color: $white;
                            text-align: center;
                            h3,h4 {
                                color: $white;
                            }
                            p {
                                margin-bottom: 0;
                            }
                            &:first-child {
                                margin-top: -$column-gutter;
                            }
                        }
                        .fields-group {
                            &.block {
                                clear: both;
                                margin: $column-gutter 0;
                                line-height: 28px;
                            }
                        }
                        .summary-group {
                            border-right: 1px solid $border;
                            font-size: 0.94rem;
                            .group-item {
                                display: block;
                            }
                            h3, h4 {
                                padding: $column-gutter/2 $column-gutter;
                                padding-bottom: 0;
                                margin: 0;
                            }
                            span {
                                padding: $column-gutter/2 $column-gutter;
                                border: 1px solid $border;
                                padding-right: 16px;
                                border-top: 0;
                                border-left: 0;
                                border-right: 0;
                                background: $module-summary;
                            }
                            .btn {
                                margin: 0;
                                background-color: $module-main;
                                border: 0;
                                border-radius: 0;
                                padding: 25px $column-gutter;
                                margin-right: -1px;
                                font-size: 1.2rem;
                                position: relative;
                                display: block;
                                &:hover {
                                    background-color: darken($module-main,10);
                                }
                                &:focus {
                                    background-color: $module-main;
                                    color: $white;
                                }
                                @include icon(arrow-right){
                                    border: 2px solid $white;
                                    border-radius: 50%;
                                    padding: 4px 0px;
                                    font-size: 2rem;
                                    position: absolute;
                                    right: 19px;
                                    top: 50%;
                                    transform: translateY(-50%);
                                    display: block;
                                }
                                &.disabled {
                                    cursor: inherit;
                                    background: $error;
                                    color: $white;
                                    &:before {
                                        content: none;
                                    }
                                    &.login {
                                        padding-right: 50px;
                                        @include icon(face){
                                            border: 0;
                                        }
                                        &:hover {
                                            cursor: pointer;
                                        }
                                    }
                                }
                            }
                            img {
                                margin: 1.875rem 0;
                                transform: translateX(-50%);
                                margin-left: 50%;
                                border-bottom: 1px solid $border;
                            }
                        }
                        .top-group {
                            h1 {
                                font-size: 2rem;
                                span {
                                    padding: $column-gutter/2 $column-gutter;
                                }
                            }
                        }
                        .main-group {
                            padding: $column-gutter;
                            border: 1px solid $border;
                            border-top: 0;
                            line-height: 28px;
                            font-size: 0.95rem;
                            p {
                                font-size: 0.95rem;
                                line-height: 28px;
                            }
                            table {
                                width: 100%;
                                tr {
                                    border-bottom: 1px solid $border;
                                    td,th {
                                        text-align: left;
                                        padding: $column-gutter/4;
                                        padding-left: 0;
                                    }
                                }
                            }
                        }
                    }
                    &.details-info {
                        .item{
                            .summary-group {
                                border: 1px solid $border;
                                .btn {
                                    margin-left: -1px;
                                    margin-top: -1px;
                                    padding-top: 18px;
                                    padding-bottom: 18px;
                                }
                            }
                            .top-group {
                                text-align: center;
                                padding: $column-gutter $column-gutter/2;
                                background: $white;
                                text-shadow: none;
                                border: 1px solid $border;
                                border-top: 0;
                                color: $text;
                                p {
                                    margin-bottom: 0;
                                    color: $text;
                                    span {
                                        font-weight: 700;
                                        padding: 0;
                                    }
                                }
                                h1,h2 {
                                    font-size: 32px;
                                    color: $main;
                                    float: none;
                                    text-shadow: none;
                                }
                                h3 {
                                    font-weight: 400;
                                    span {
                                        background: none;
                                        padding-top: 0;
                                        padding-bottom: 0;
                                    }
                                }
                            }
                            .main-group {
                                p, div > span {
                                    margin-bottom: $column-gutter;
                                    display: block;
                                }
                                .accented_details {
                                    padding: $column-gutter;
                                    margin: $column-gutter (-$column-gutter);
                                    margin-bottom: $column-gutter;
                                    background: $main;
                                    color: $white;
                                    text-align: center;
                                    h3,h4 {
                                        color: $white;
                                    }
                                    p {
                                        margin-bottom: 0;
                                    }
                                    &:first-child {
                                        margin-top: -$column-gutter;
                                    }
                                }
                            }
                            .foreigncollection {
                                padding: $column-gutter;
                                margin: $column-gutter (-$column-gutter);
                                border-top: 1px solid $border;
                                border-bottom: 1px solid $border;
                                h2 {
                                    color: $main;
                                }
                                p:last-child {
                                    margin-bottom: 0;
                                }
                                & + .foreigncollection {
                                    margin-top: -$column-gutter;
                                    border-top: 0;
                                }
                            }
                        }
                    }
                    & + .module-container.mode-uniquerequested {
                        h2.title {
                            margin-top: $column-gutter;
                        }
                        .items .item {
                            margin-top: 0;
                            margin-bottom: $column-gutter;
                            padding-left: $column-gutter/2;
                            padding-right: $column-gutter/2;
                            .fields-group {
                                width: 100%;
                            }
                            .top-group {
                                h1 span {
                                    padding: $column-gutter/2;
                                    text-align: left;
                                }
                            }
                            .main-group {
                                padding: $column-gutter/2;
                            }
                        }
                    }
                }
            }
        }
    }
}

#wrapper div#main section div.vacancyapplication .fields .fieldsgroup.fieldsgroup_skill .fields .field .wrapper {
    .value {
        width: 100%;
        padding: 0px;
        * {

        }
    }
    .label {
        display: none;
    }
}

@media #{$medium-up} {
    #wrapper {
        div#main {
            section {
                div.overview {
                    .field {
                        .wrapper {
                            .label {
                                @include grid-column(3);
                                min-height: 1px;
                            }
                            .value {
                                @include grid-column(9);
                                padding-left: 0;
                            }
                        }
                        &.foreignfield {
                            padding-bottom: $column-gutter/2;
                            .wrapper {
                                line-height: 38px;
                            }
                            .label {
                                @include grid-column(3);
                            }
                            .value {
                                @include grid-column(4);
                                padding-left: 0;
                            }
                            .actions {
                                @include grid-column(5);
                            }
                        }
                        &.vacancyapplication_foreignfield {
                            .label {
                                @include grid-column(3);
                                padding-left: 0;
                            }
                            .value {
                                @include grid-column(9);
                                padding-left: 0;
                            }
                        }
                    }
                    &.Skill_overview {
                        .wrapper {
                            .value {
                                @include grid-column(7);
                                padding-left: 0;
                            }
                        }
                    }
                    &.account_overview {
                        & + .actions {
                            @include grid-row();
                            float: none;
                            a.btn {
                                width: 200px;
                            }
                        }
                    }
                }
                .module-container {
                    .items {
                        .item {
                            & > a {
                                @include clearfix;
                            }
                            .top-group, .main-group, .bottom-group {
                                @include clearfix;
                                float: left;
                                width: 100%;
                            }
                            .top-group {
                                span {
                                    display: inline-block;
                                    vertical-align: top;
                                }
                                h1 {
                                    float: left;
                                    line-height: inherit;
                                }
                            }
                            .photo_wrapper {
                                width: 26%;
                                float: left;
                            }
                            .main-group {
                                width: 74%;
                                float: right;
                            }
                            .bottom-group {
                                @include clearfix;
                                font-size: 0.9rem;
                                text-align: right;
                                padding: 0;
                                span, a, label, .side {
                                    display: inline-block;
                                    float: left;
                                    border-bottom: 0;
                                }
                                a.btn, .side {
                                    float: none;
                                }
                                label, label+span, label+a.inline {
                                    width: auto;
                                }
                                label+span, label+a.inline {
                                    border-right: 0;
                                    float: left;
                                }
                                label {
                                    border-left: 1px solid $border;
                                    font-weight: 700;
                                }
                                a.btn-icon {
                                    font-size: 0rem;
                                    vertical-align: top;
                                    line-height: initial;
                                    padding: 15px;
                                    &:before {
                                        font-size: 1.3rem;
                                        margin: 0;
                                    }
                                }
                                .formcontainer {
                                    float: left;
                                    form {
                                        .formfield {
                                            width: auto;
                                            label {
                                                border-left: none;
                                            }
                                            select {
                                                float: left;
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }
                    &.layout-blocks {
                        .items {
                            .item {
                                @include grid-column(6);
                                .photo_wrapper {
                                    width: 100%;
                                }
                                .bottom-group {
                                    text-align: left;
                                    display: flex;
                                    & > span {
                                        display: inline-block;
                                        width: 50%;
                                    }
                                }
                            }
                        }
                    }
                    &.mode-uniquerequested {
                        .items {
                            .item {
                                padding: 0;
                                .main-group {
                                    width: 100%;
                                }
                                .top-group h1 {
                                    line-height: 1.4;
                                }
                            }
                        }
                        &.details-info {
                            .item{
                                .top-group {
                                    h3 {
                                        span {
                                            border-left: 1px solid $text;
                                            &:first-child {
                                                border-left: 0;
                                            }
                                        }
                                    }
                                }
                            }
                        }
                        & + .module-container.mode-uniquerequested {
                            .items .item {
                                width: 50%;
                                display: inline-block;
                                .main-group {
                                    min-height: 151px;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

