#wrapper {
    &[class*="vacancy-overview"][class*="-container"]{
        .bottombar {
            @include clearfix;
            width: 100%;
        }
        .legend {
            list-style-type: none;
            margin: $column-gutter 0 0 $column-gutter/2;
            li {
                &:before { 
                    content: "";
                    display: inline-block;
                    width: 11px;
                    height: 11px;
                    border-radius: 50%;
                    margin-right: 10px;
                }
                &.vrijwilligerswerk:before {
                    background: $vrijwilligerswerk;
                }
                &.maatschappelijke-stage:before {
                    background: $maatschappelijke-stage;
                }
                &.groepsvacature:before {
                    background: $groepsvacature;
                }
                &.leerplek:before, &.leerwerkplek:before {
                    background: $leerplek;
                }
            }
        }
    }
    div#main {
        section {
            div#searchbar {
                border-top: 1px solid $border;
                div.wrapper {
                    @include grid-row();
                    form.searchform_form {
                        @include grid-row();
                        div.viewmode {
                            @include grid-column(12);
                            min-height: 70px;
                            .countfield{
                                select{
                                    width: 20%;
                                }
                                label{
                                    margin-right: 20px;
                                }
                            }
                            .overview_typefield {
                                label {
                                    display: inline-block;
                                    margin: 8px 25px 8px 0;
                                }
                                .inline_wrapper {
                                    display: inline-block;
                                    .option_field {
                                        display: inline-block;
                                        input[value="overview"] + label {
                                            @include icon(view-detail){
                                                font-size: 32px;
                                            };
                                        }
                                        input[value="listoverview"] + label {
                                            @include icon(view-list){
                                                font-size: 28px;
                                            };
                                        }
                                        input[value="blocks"] + label {
                                            @include icon(view-blocks){
                                                font-size: 30px;
                                            };
                                        }
                                        [type="radio"] + label:before {
                                            border: 0;
                                            font-size: 26px;
                                            color: lighten($text,10);
                                        }
                                        [type="radio"]:checked + label:after {
                                            border: 0;
                                            background-color: transparent;
                                        }
                                        [type="radio"]:checked + label:before {
                                            color: $radio-checked;
                                        }
                                        label {
                                            padding-left: 0;
                                            margin-right: 33px;
                                            span {
                                                font-size: 0rem;
                                            }
                                        }
                                    }
                                }
                            }
                        }
                        div.filter {
                            @include grid-column(12);
                            padding-top: 5px;
                            input {
                                height: 47px;
                            }
                            .distancefield {
                                .chosen-container {
                                    width: 100% !important;
                                    margin-top: 0;
                                    .chosen-search {
                                        input {
                                            height: auto;
                                        }
                                    }
                                }
                            }
                            .submit_field {
                                button {
                                    width: 50%;
                                    float: right;
                                    padding: 12px 20px;
                                    margin-top: 20px;
                                }
                            }
                        }
                    }
                }
            }
            .brandbox {
                position: relative;
                #searchbar.scrollCompanion {
                    background-color: transparent;
                    border-bottom: 0;
                    border-top: 0;
                    margin: 90px 0 15px;
                    padding: 0;
                    h1 {
                        @include grid-column(12);
                        font-size: 2.125rem;
                        padding: 0;
                        span {
                            font-size: 2.125rem;
                        }
                    }
                    .wrapper {
                        .formcontainer {
                            background: transparent;
                            .filter {
                                padding: 0;
                                border-top: 1px solid rgba(255,255,255,0.4);
                            }
                            .text_field, .select_field {
                                @include grid-column(12);   
                                margin: $column-gutter 0 0;
                                margin-bottom: 0;
                                input, select {
                                    border-bottom: 0;
                                    background-color: $white;
                                    color: $text;
                                    padding-left: 17px;
                                    padding-top: 0; 
                                }
                                label {
                                    color: $text;
                                    top: 11px;
                                    left: 34px;
                                    &.active {
                                        color: $white;
                                        font-size: 1rem;
                                        left: 17px;
                                        transform: translateY(-168%);
                                    }
                                }
                                .bar {
                                    display: none;
                                }
                            }
                            .select_field select {
                                background-position: 100% 50%;
                            }
                            .submit_field {
                                @include grid-column(12);
                                margin-top: 0;
                                margin-bottom: $column-gutter;
                                button {
                                    @include btn($secondButton);
                                    padding: 12px 20px;
                                    width: 100%;
                                    margin-top: 0;
                                    line-height: inherit; 
                                    &:before {
                                        content: "";
                                    }
                                }
                            }
                            .bottombar {
                                color: $text;
                                background: $white;
                                clear: both;
                                position: relative;
                                bottom: -15px;
                                width: 100%;
                                ul.legend {
                                    display: inline-block;
                                    vertical-align: top;
                                    font-size: 14px;
                                    margin: 13px 0 0 $column-gutter/2;
                                }
                                .viewmode {
                                    display: inline-block;
                                    vertical-align: top;
                                    float: none;
                                    width: auto;
                                    min-height: inherit;
                                    .formfield {
                                        display: inline-block;
                                        margin: 0;
                                        margin-right: 35px;
                                        font-size: 14px;
                                        padding-bottom: 0;
                                        width: auto;
                                        select {
                                            width: auto;
                                        }
                                        &:last-child {
                                            margin-right: 0;
                                        }
                                        .option_field {
                                            margin-right: 0;
                                        }
                                        label {
                                            margin-bottom: 0;
                                            vertical-align: middle;
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
            .notice {
                @include grid-column(12);
                float: none;
                border:1px solid #eff1c0;
                background-color:#fffeec;
                padding: 15px;
                margin-top: 10px;
                min-height: 25px;
                height: 100%;
                color: $text;
                margin-bottom: 20px;
                width: 97%;
                @include icon(warning){
                    margin-right: 20px;
                    vertical-align: bottom;
                };
            }

            div#searchbar.vacancy-detail {
                background-color: $main;
                div.wrapper {
                    h1 {
                        color: $white;
                    }
                    a.scrollCompanionToggle{
                        border-color: $white;
                        color: $white;
                    }
                    form.searchform_form {
                        div.filter {
                            input {
                                border-color: $white;
                                color: $white;
                            }
                            select {
                                color: $white;
                                option {
                                    color: $text;
                                }
                            }
                            label {
                                color: $white;
                                &.active {
                                    color: $text;
                                }
                            }
                            .bar:before, .bar:after {
                                background: $white;
                            }
                        }
                    }
                }
            }
            div#filter {
                padding-top: 36px;
                margin-bottom: $column-gutter;
                .wrapper {
                    padding-top: 0;
                }

                h2 {
                    border-bottom: 1px solid $border;
                    background-color: $white;
                    border: 1px solid $border;
                    margin: 0;
                    padding: 18px;
                    font-size: 1.5rem;
                }
                form {
                    border-left: 1px solid $border;
                    border-right: 1px solid $border;
                    background-color: $white;
                    ul.filter_filters {
                        margin: 0;
                        padding: 0;
                        & > li {
                            background: $filterBG;
                            @include transition();
                            -webkit-backface-visibility: hidden;
                            @include icon('arrow-down'){
                                @include rotate(180deg);
                                float: right;
                                margin-top: 6px;
                                margin-right: 10px;
                                font-size: 26px;
                                color: $main;
                                @include transition();
                            };
                            & > span.filter_label {
                                display: block;
                                padding: 0 18px 0 29px;
                                line-height: 51px;
                                border-bottom: 1px solid $border;
                                position: relative;
                                &:before {
                                    content: "";
                                    display: inline-block;
                                    height: 51px;
                                    position: absolute;
                                    left: 0;
                                    border-left: 9px solid $filterNonActive;
                                    border-right: 1px solid $border;
                                }
                                &:hover {
                                    cursor: pointer;
                                }
                            }
                            &.active {
                                background: $filterBG;
                                &:before {
                                    @include rotate(180deg);
                                    @include transition();
                                }
                                & > span.filter_label {
                                    &:before {
                                        border-left: 9px solid $filterActive;
                                    }
                                }
                                & > .filter_filter {
                                    display: block;
                                    li.filter_remove {
                                        padding-left: 20px;
                                    }
                                }
                            }
                            &.collapsedFilter {
                                background: $white;
                                @include transition();
                                &:before {
                                    @include rotate(0deg);
                                    @include transition();
                                }
                                & .filter_filter {
                                    display: none;
                                }
                            }
                            ul {
                                border-bottom: 1px solid $border;
                                padding: 9px 0px;
                                margin: 0;
                                li {
                                    line-height: 37px;
                                    span.filter_label {
                                        display: inline-block;
                                        vertical-align: middle;
                                        max-width: 270px;
                                        padding-left: 18px;
                                        border: none;
                                        label {
                                            max-width: 250px;
                                        }
                                    }
                                    span.filter_amount {
                                        display: inline-block;
                                        vertical-align: middle;
                                    }
                                    &.filter_zero {
                                        display: none;
                                    }
                                }
                            }
                        }

                    }
                    ul li { list-style-type: none; }
                }
                .jobalert_button {
                    background: $white;
                    width: 100%;
                    float: none;
                    padding: 10px;
                    border: 1px solid $border;
                    border-top: 0;
                    &.jobalert_button_under_filter {
                        padding: 0;
                        padding-top: 10px;
                        .wrapper {
                            height: 100%;
                            padding-top: 0;
                            padding-left: 10px;
                            padding-right: 10px;
                            padding-bottom: 4px;
                            a.button {
                                width: 100%;
                                text-align: center;
                            }
                        }
                    }
                    a.btn {
                        @include btn($secondButton);
                        background-color: $jobalertBtn;
                        display: block;
                        padding: 10px 20px;
                        font-size: 1rem;
                        text-align: center;
                        &:hover {
                            background-color: lighten($jobalertBtn,10);
                        }
                    }
                }
                .filter_reset_button {
                    border-bottom: 1px solid $border;
                    text-align: center;
                    .wrapper {
                        height: 100%;
                        padding-top: 0;
                        padding-left: 10px;
                        padding-right: 10px;
                        padding-bottom: 4px;
                        padding-top: 10px;
                        a.btn {
                            width: 100%;
                        }
                    }
                }
            }
            aside + .notice {
                margin-top: 36px;
            }
            .module-container.vacancy-module {
                .item{
                    .legend {
                        margin: 0;
                        line-height: inherit;
                        display: inline-block;
                        float: right;
                        &:before {
                            content: "";
                            border-left: 1px solid $border;
                            display: inline-block;
                            width: 2px;
                            position: absolute;
                            left: 0;
                            height: 100%;
                            top: 0;
                        }
                        li {
                            vertical-align: top;
                            position: relative;
                            margin-right: 15px;
                            display: inline-block;
                            &:before {
                                height: 11px;
                                width: 11px;
                                border-radius: 50%;
                                margin: 0;
                            }
                            &:last-child {
                                margin-right: 0;
                            }
                        }
                    }
                    .bottom-group {
                        span {
                            &:before {
                                display: inline-block;
                                vertical-align: top;
                            }
                        }
                        .contracttype,
                        .workshop_type {
                            border-right: none;
                            background-color: #c9c9c9;
                            color: $text;
                            .vakantiewerk,
                            .tijdelijk-met-uitzicht-op-vast {
                                background-color: $main;
                                color: $white;
                            }
                            .stageplaats {
                                background-color: $mainButton;
                                color: $white;
                            }
                            .vrijwilligerswerk {
                                background-color: #008000;
                                color: $white;
                            }
                            .vast---tijdelijk,
                            .vast-tijdelijk {
                                background-color: #008000;
                                color: $white;
                            }
                            .vast {
                                background-color: #008000;
                                color: $white;
                            }
                            .tijdelijk {
                                background-color: #008000;
                                color: $white;
                            }
                            span {
                                padding: 10px;
                            }
                        }
                    }
                }
                &.mode-uniquerequested .item{
                    .bottom-group {
                        margin-bottom: $column-gutter;
                        p {
                            padding: $column-gutter;
                            margin-bottom: 0;
                            &.small{
                                text-align: center;
                                font-size: 0.8rem;
                            }
                        }
                        a.btn {
                            display: block;
                            text-align: center;
                            border: 1px solid $border;
                            border-color: darken($secondButton,20);
                            color: $white;
                            background: $secondButton;
                            &:hover {
                                color: $white;
                                background: darken($secondButton,10);
                            }
                        }
                    }
                }
            }
            section#overview {
                padding-top: 36px;
                padding-right: 0;
                a.vacancy_item {
                    display: block;
                    @include transition();
                }
                article.card {
                    background-color: #EDEDED;
                    border: 1px solid $border;
                    margin: $column-gutter $column-gutter/2;
                    padding: 0;
                    @include transition();
                    cursor: pointer;
                    position: relative;
                    -webkit-backface-visibility: hidden;
                    &:hover {
                        box-shadow: 0.868px 4.924px 5.34px 0.66px rgba(0, 0, 0, 0.11);
                    }
                    &:first-child {
                        margin-top: 0;
                    }
                    header {
                        background-color: $main;
                        h1 {
                            color: $white;
                            font-size: 1.2rem;
                            font-weight: normal;
                            padding: $column-gutter/2;
                            margin: 0;
                            font-weight: bold;
                        }
                    }
                    .carditem-main,
                    .workshopDescription {
                        background: $white;
                        color: $text;
                        p {
                            margin-bottom: 0;
                            font-weight: normal;
                            padding: $column-gutter/2 0;
                            display: inline-block;
                        }
                        img {
                            margin: 0 auto;
                            display: block;
                        }
                    }
                    .carditem-main,
                    .workshopDescription {
                        .field {
                            &.functiondescription_intro_field,
                            &.description_field {
                                .value {
                                    margin-left: 15px;
                                    margin-right: 15px;
                                }
                            }
                        }
                        p {
                            margin-left: 15px;
                            margin-right: 15px;
                        }
                    }
                    
                    .carditem-bottom,
                    .workshopInformation {
                        padding: 0;
                        padding-left: 15px;
                        line-height: 0;
                        position: relative;
                        min-height: 45px;
                        height: 45px;
                        overflow: hidden;
                        font-size: 13px;
                        background: $vacancyBottombar;
                        a {
                            display: inline-block;
                            vertical-align: middle;
                        }
                        .field { 
                            display: inline-block;
                            line-height: 22px;
                            vertical-align: top;
                            height: 45px;
                            position: relative;
                            color: $text;
                            .wrapper {
                                padding-top: 12px;
                                padding-bottom: 0;
                            }
                            &.workinglocation_field,
                            &.location_district_field {
                                padding-left: 0;
                            }
                            &.contracttype_field,
                            &.workshop_type_field {
                                border-right: none;
                                padding: 0;
                                .value {
                                    background-color: #c9c9c9;
                                    color: $text;
                                }
                                .vakantiewerk,
                                .tijdelijk-met-uitzicht-op-vast {
                                    background-color: $main;
                                    color: $white;
                                }
                                .stageplaats {
                                    background-color: $mainButton;
                                    color: $white;
                                }
                                .vrijwilligerswerk {
                                    background-color: #008000;
                                    color: $white;
                                }
                                .vast---tijdelijk,
                                .vast-tijdelijk {
                                    background-color: #008000;
                                    color: $white;
                                }
                                .vast {
                                    background-color: #008000;
                                    color: $white;
                                }
                                .tijdelijk {
                                    background-color: #008000;
                                    color: $white;
                                }
                                span {
                                    padding: 10px;
                                }
                            }
                            &.workinglocation_field {
                                .value {
                                    @include icon(mapmarker){
                                        margin-right: 10px;
                                    };
                                }
                            }
                            &.amounthours_field {
                                .value {
                                    @include icon("time"){
                                        margin-right: 10px;
                                    };
                                }
                            }
                            &.tariff_field {
                                .value {
                                    @include icon(euro){
                                        margin-right: 10px;
                                        vertical-align: top;
                                    };
                                }
                            }
                            &.functiondescription_intro_field .wrapper {
                                padding-top: 0;
                                padding-bottom: 0;
                            }
                        }
                        .divider {
                            border-left: 1px solid $border;
                            display: inline-block;
                            width: 2px;
                            margin-left: 15px;
                            margin-right: 15px;
                            height: 45px;
                        }
                    }
                    footer {
                        display: none;
                        padding-right: $column-gutter/2;
                        padding-bottom: $column-gutter/3;
                        padding-top: $column-gutter/3;
                        width: 52%;
                        a.btn {
                            @include btn($vacancyBtn, $text, $greyBtn:1);
                            font-weight: bold;
                            width: 48%;
                            &:last-child {
                                float: right;
                            }
                        }
                    }
                }
            }
            div.details.carditem,
            div.details.workshop{
                &.vacancyapplication {
                    display: none;
                }
                @include grid-row();
                margin-top: $column-gutter;
                .maincolumn {
                    @include grid-column(12);
                    .detailscolumn {
                        padding: 0;
                        .infobar,
                        .workshopinfobar {
                            border-bottom: 1px solid $border;
                            .field {
                                height: 100%;
                                padding: 10px;
                                .value span {
                                    padding-left: 10px;
                                    &:before {
                                        background-image: none !important;
                                        font: normal normal normal 14px/1 FontAwesome;
                                        color: $main;
                                        float: left;
                                        padding-top: 3px;
                                    }
                                }
                                &.empty_field {
                                    display: none;
                                }
                                &.workinglocation_field,
                                &.location_district_field {
                                    position: relative;
                                    border-right: 1px solid $border;
                                    float: left;
                                    width: 18%;
                                    .value span:before {
                                        content: "\f124";
                                    }
                                }
                                &.amounthours_field,
                                &.enddate_field {
                                    float: left;
                                    border-right: 1px solid $border;
                                    .value span:before {
                                        content: "\f017";
                                    }
                                }
                                &.amountdays_field {
                                    float: left;
                                    .value span:before {
                                        content: "\f073";
                                    }
                                }
                                &.status_field {
                                    width: 10%;
                                    float: right;
                                    text-transform: uppercase;
                                    border-left: 1px solid $border;
                                    .value {
                                        span {
                                            color: $main;
                                            font-weight: bold;
                                            padding-left: 0;
                                        }
                                    }
                                }
                            }
                        }
                        .infobar.training {
                            padding: $column-gutter/2;
                            border-bottom: 1px solid $border;
                            height: 54px;
                            ul {
                                margin: 0;
                                list-style: none;
                                li {
                                    display: inline-block;
                                    font-weight: 700;
                                    &.location {
                                        position: relative;
                                        &:before{
                                            display: block;
                                            float:left;
                                            content: "";
                                            margin: 2px 10px 0 0;
                                            @include icon("mapmarker");
                                        }
                                    }
                                    &.amountpositions {
                                        margin-left: 10px;
                                        border-left: 1px solid $text;
                                        padding-left: 10px;
                                        margin-left: 10px;
                                    }
                                    &.status {
                                        display: none;
                                        float: right;
                                        text-transform: uppercase;
                                        color: $main;
                                        border-left: 1px solid $border;
                                        margin-top: - $column-gutter/2;
                                        margin-bottom: - $column-gutter/2;
                                        padding:  $column-gutter/2 0;
                                        padding-left:  $column-gutter/2;
                                    }
                                }
                            }
                        }
                        .function,
                        .workshopfunction {
                            text-align: center;
                            padding: $column-gutter $column-gutter/2;
                            h1 {
                                font-size: 32px;
                                margin-bottom: 28px;
                                font-weight: 100;
                                word-break: break-word;
                            }
                            p {
                                margin-bottom: 0;
                            }
                            h2 {
                                font-size: 29px;
                                font-weight: 100;
                                color: $main;
                            }
                            h3 {
                                font-weight: 100;
                            }
                        }
                        & > .field {
                            padding:  $column-gutter;
                            padding-top: 0;
                            h2, .label span{
                                font-size: 1rem;
                                color: $text;
                                font-weight: 700;
                            }
                            h2.label, .wrapper {
                                margin-top: 30px;
                            }
                            &.foreigncollection {
                                border-top: 1px solid $border;
                                padding-top: $column-gutter;
                                & > .wrapper > .label span{
                                    font-size: 22px;
                                    font-weight: 100;
                                    color: #713e7e;
                                }
                                .field .wrapper {
                                    margin: 10px 0;
                                }
                            }
                        }
                        .title_field {
                            display: none;
                        }
                        .underdetails,
                        .underworkshopdetails {
                            width: 100%;
                            float: none;
                            border-top: 1px solid $border;
                            padding: $column-gutter/2;
                            text-align: right;
                            .apply_text {
                                float: left;
                                text-align: left;
                                padding: 12px;
                            }
                            a.btn {
                                @extend .button;
                            }
                            .apply_text,
                            .download_form {
                                margin: 0;
                                text-align: center;
                                float: none;
                            }
                        }
                    }
                }
                .accented_details {
                    padding: $column-gutter;
                    margin-bottom: $column-gutter;
                    background: $main;
                    color: $white;
                    text-align: center;
                    .label{
                        font-weight: bold;
                        span {
                            color: $white;
                        }
                    }
                    .field {
                        margin: 10px 0;
                        &.empty_field {
                            display: none;
                        }
                        .wrapper {
                            .value {
                                color: $white;
                            }
                        }
                    }
                }
                .rightcolumn {
                    @include grid-column(12);
                    .information {
                        .field{
                            margin-bottom: 10px;
                            .label{
                                font-weight: 700;

                            }
                        }
                    }
                    .photo {
                        margin-bottom: $column-gutter/2;
                    }
                    .contentblock {
                        @include card;
                    }
                    .empty_field{
                        display: none;
                    }
                    & > a {
                        @extend .button;
                        width: 100%;
                        text-align: center;
                        margin-bottom: 20px;
                    }
                }
            }
        }
    }
}

@media #{$medium-up} {
    #wrapper {
        &[class*="vacancy-overview"][class*="-container"]{
            .legend {
                li {
                    display: inline-block;
                    margin-right: 30px;
                }
            }
        }
        div#main {
            section {
                div#searchbar {
                    div.wrapper {
                        form.searchform_form {
                            div.viewmode {
                                @include grid-column(5);
                            }
                            div.filter {
                                @include grid-column(7);
                                .distancefield {
                                    display: inline-block;
                                    float: left;
                                    width: 37%;
                                    margin-right: 16px;
                                }
                                .submit_field {
                                    display: inline-block;
                                    float: left;
                                    width: 57%;
                                    button {
                                        margin-top: 0px;
                                    }
                                }
                            }
                        }
                    }
                }
                .module-container.vacancy-module {
                    &.mode-uniquerequested .item{
                        
                    }
                }
                section#overview {
                    article {
                        @include clearfix();
                        .carditem-main {
                            @include clearfix();
                            img {
                                @include grid-column(2);
                                padding: $column-gutter/2 0;
                            }
                            .field {
                                @include grid-column(10);
                                padding-left: 0;
                                padding-right: 0;
                            }
                        }
                        .carditem-bottom {
                            padding-top: 2rem;
                            padding-left: 0;
                            p {
                                margin-bottom: 0;
                            }
                            a.vacancy_item {
                                display: inline-block;
                            }
                            #favorite_wrapper {
                                height: 20px;
                            }
                        }
                        footer {
                            @include grid-column(6);
                            padding-top: $column-gutter/2;
                            padding-right: 0;

                            a.btn {

                            }
                        }
                    }
                }
                div.details.carditem,
                div.details.workshop {
                    .maincolumn {
                        @include grid-column(8);
                        .details {
                            .infobar {
                                ul li.status{
                                    display: block;
                                }
                            }
                        }
                    }
                    .rightcolumn {
                        @include grid-column(4);
                    }
                }
            }
        }
    }
}

@media #{$large-up} {
    #wrapper {
        div#main {
            section {
                div#searchbar {
                    div.wrapper {
                        form.searchform_form {
                            div.filter {
                                .distancefield {
                                    width: 23%
                                }
                                .submit_field {
                                    width: 74%
                                }
                            }
                        }
                    }
                }
                .module-container.vacancy-module {
                    &.mode-uniquerequested .item{
                    }
                }
                section#overview {
                    article {
                        margin-left: 0;
                        margin-right: 0;
                        .carditem-bottom {
                            padding-left: 0;
                            padding-right: 0;
                        }
                        footer {
                            @include grid-column(7);
                            padding-left: 0;
                            padding-right: 0;
                            position: relative;
                        }
                    }
                }
            }
        }
    }
}